  position: fixed;
  top: calc(100vh - 56px);
  bottom: 0;
  width: 100%;
} */

.App {
  text-align: center;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(5px + 2vmin);
}

.App-link {
  color: #D33D3B;
}

